.shopping-list-header {
  color: #111;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
}

.js-books {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.pagination {
  gap: 14px;
  padding-bottom: 40px;
  display: flex;
}

.pagination-btns, .pagination-numer-btns {
  gap: 8px;
  display: flex;
}

.pagination-btn {
  color: #000;
  width: 38px;
  height: 38px;
  background-color: #fff;
  border-width: 1px;
  border-color: #000;
  border-radius: 50%;
  font-family: DM Sans;
  font-size: 18px;
  font-weight: 700;
}

.first-btn, .prev-btn, .prev-numer-btn {
  color: #fff;
  background-color: #111;
}

.first-btn, .prev-btn, .next-btn, .last-btn {
  font-weight: 400;
}

.next-btn, .last-btn {
  background-color: #eac645;
}

body.dark-theme .shopping-list-header {
  color: #fff;
}

.empty-shopping-list-div {
  padding: 80px 55px;
}

.empty-shopping-list-msg {
  text-align: center;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 14px;
}

body.dark-theme .empty-shopping-list-msg {
  color: rgba(255, 255, 255, .6);
}

.empty-shopping-list-img {
  width: 265px;
  height: 198px;
}

.title-color {
  color: #4f2ee8;
}

.support-ukraine {
  display: none;
}

.shopping-book-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shoping-book-card {
  width: 335px;
  height: 170px;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  gap: 14px;
  padding: 14px;
  display: flex;
  position: relative;
}

.shoping-book-card-img > img {
  width: 100px;
  height: 142px;
}

.shoping-book-card-text {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.shoping-book-card-ganre {
  color: #b4afaf;
  width: 144px;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  display: inline-block;
  overflow: hidden;
}

.shoping-book-card-title_and_ganre {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.shoping-book-card-title {
  color: #111;
  width: 144px;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 16px;
  font-weight: 700;
  transition: all .4s;
  display: inline-block;
  overflow: hidden;
}

body.dark-theme .shoping-book-card-title {
  color: #fff;
}

.shoping-book-card-description {
  color: #111;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 14px;
  font-weight: 400;
  display: -webkit-box;
  overflow: hidden;
}

body.dark-theme .shoping-book-card-description {
  color: #fff;
}

.shoping-book-card-author {
  color: #b4afaf;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
}

.shoping-list-btn {
  width: 28px;
  height: 28px;
  stroke: #fff;
  fill: #4f2ee8;
  background-color: #4f2ee8;
  background-image: url("icon-basket.137ce1c8.png");
  background-position: center;
  background-repeat: no-repeat;
  border: rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 14px;
  right: 14px;
}

.shoping-list-images {
  position: absolute;
  bottom: 14px;
  right: 14px;
}

.list-images {
  align-items: center;
  gap: 8px;
  display: flex;
}

.image-link {
  display: block;
}

.image-link-amazon {
  width: 32px;
  height: 11px;
  background-image: url("image-amazon.6aea004d.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.image-link-openBook {
  width: 16px;
  height: 16px;
  background-image: url("image-open-book.99f5b86b.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.image-link-bookShop {
  width: 16px;
  height: 16px;
  background-image: url("image-book-shop.579c7059.png");
  background-repeat: no-repeat;
  background-size: contain;
}

@media screen and (min-width: 768px) {
  .pagination {
    gap: 24px;
  }

  .pagination-btn {
    width: 44px;
    height: 44px;
  }

  .shopping-list-header {
    font-size: 48px;
  }

  .shopping-list.section {
    padding-top: 0;
  }

  .empty-shopping-list-div {
    width: 437px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
  }

  .empty-shopping-list-msg {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .empty-shopping-list-img {
    width: 322px;
    height: 241px;
  }

  .shoping-book-card {
    width: 704px;
    height: 213px;
    gap: 24px;
    padding: 24px;
  }

  .shoping-book-card-img > img {
    width: 116px;
    height: 165px;
  }

  .shoping-book-card-ganre {
    padding-bottom: 10px;
  }

  .image-link-bookShop {
    width: 32px;
    height: 30px;
  }

  .image-link-openBook {
    width: 28px;
    height: 27px;
  }

  .image-link-amazon {
    width: 43px;
    height: 15px;
  }

  .shoping-list-images {
    bottom: 24px;
    right: 24px;
  }

  .shoping-list-btn {
    width: 34px;
    height: 34px;
    top: 24px;
    right: 24px;
  }

  .shoping-book-card-title, .shoping-book-card-ganre {
    width: 100%;
  }

  .list-images {
    gap: 16px;
  }
}

@media screen and (min-width: 1440px) {
  .shopping-list {
    width: 100%;
  }

  .empty-shopping-list-div {
    margin: 0 auto;
  }

  .shopping-list-div {
    width: 996px;
  }

  .container-shop {
    width: 1440px;
    justify-content: left;
    align-items: start;
    margin: 0 auto;
    padding: 0 24px;
    display: flex;
  }

  .shop-list-support-ukraine > section.support-ukraine {
    margin: 0;
    display: block;
  }

  div.shopping-list-div {
    padding-left: 40px;
  }

  .book-card-description {
    width: 808px;
  }

  .shopping-list-header {
    margin-top: 0;
  }

  .container-shop {
    margin-top: 40px;
  }

  .shoping-book-card {
    width: 996px;
  }

  .shoping-book-card-img > img {
    width: 116px;
    height: 165px;
  }

  .shoping-book-card-ganre {
    padding-bottom: 10px;
  }

  .list-images {
    gap: 20px;
  }
}

